"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { useState } from "react";
import { useAuth } from "@/lib/context/AuthContext";

export default function Navbar() {
  const pathname = usePathname();
  const { user, loading, signOut } = useAuth();
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  const navItems = [
    { href: "/nasil-calisir", label: "Nasıl Çalışır?" },
    { href: "/hakkimizda", label: "Hakkımızda" },
    { href: "/iletisim", label: "İletişim" },
  ];

  const handleSignOut = async () => {
    try {
      await signOut();
    } catch (error) {
      console.error('Çıkış hatası:', error);
    }
  };

  const getDashboardLink = () => {
    if (!user) return "/dashboard";
    if (user.role === "admin") return "/admin";
    if (user.role === "producer") return "/producer";
    return "/dashboard";
  };

  return (
    <>
      <nav className="fixed left-0 right-0 top-0 z-50 border-b border-[#d2d2d7]/40 bg-[#fbfbfd]/80 backdrop-blur-xl backdrop-saturate-[180%]">
        <div className="mx-auto flex h-[48px] max-w-[980px] items-center justify-between px-6">
          
          <Link 
            href="/" 
            className="text-[21px] font-semibold tracking-[-0.01em] text-[#1d1d1f] transition-opacity duration-200 hover:opacity-60"
          >
            ANIKOD
          </Link>

          <div className="hidden items-center gap-8 md:flex">
            {navItems.map((item) => (
              <Link
                key={item.href}
                href={item.href}
                className={`relative text-[14px] font-normal leading-[1.43] transition-colors duration-200 ${
                  pathname === item.href
                    ? "text-[#1d1d1f]"
                    : "text-[#6e6e73] hover:text-[#1d1d1f]"
                }`}
              >
                {item.label}
                {pathname === item.href && (
                  <span className="absolute -bottom-[14px] left-0 right-0 h-[2px] bg-[#0071e3] rounded-full" />
                )}
              </Link>
            ))}
          </div>

          <div className="flex items-center gap-3">
            {loading ? (
              <div className="h-[32px] w-[80px] animate-shimmer rounded-full" />
            ) : user ? (
              <div className="hidden items-center gap-3 sm:flex">
                <Link
                  href={getDashboardLink()}
                  className="inline-flex h-[32px] items-center justify-center rounded-full bg-[#0071e3] px-4 text-[12px] font-normal text-white transition-all duration-200 hover:bg-[#0077ed] hover:scale-[1.02] active:scale-[0.98]"
                >
                  Panel
                </Link>
                <button
                  onClick={handleSignOut}
                  className="inline-flex h-[32px] items-center justify-center rounded-full border border-[#d2d2d7] bg-transparent px-4 text-[12px] font-normal text-[#1d1d1f] transition-all duration-200 hover:border-[#86868b] hover:bg-[#f5f5f7]"
                >
                  Çıkış
                </button>
              </div>
            ) : (
              <Link
                href="/auth/login"
                className="hidden h-[32px] items-center justify-center rounded-full bg-[#0071e3] px-4 text-[12px] font-normal text-white transition-all duration-200 hover:bg-[#0077ed] hover:scale-[1.02] active:scale-[0.98] sm:inline-flex"
              >
                Giriş Yap
              </Link>
            )}

            <button
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
              className="flex h-[32px] w-[32px] items-center justify-center rounded-lg text-[#1d1d1f] transition-colors hover:bg-[#f5f5f7] md:hidden"
              aria-label="Menü"
            >
              <svg className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1.5}>
                {mobileMenuOpen ? (
                  <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
                ) : (
                  <path strokeLinecap="round" strokeLinejoin="round" d="M4 6h16M4 12h16M4 18h16" />
                )}
              </svg>
            </button>
          </div>

        </div>
      </nav>

      {mobileMenuOpen && (
        <div 
          className="fixed inset-0 z-40 bg-black/20 backdrop-blur-sm md:hidden"
          onClick={() => setMobileMenuOpen(false)}
        >
          <div 
            className="absolute right-0 top-[48px] w-full max-w-[300px] animate-slide-in-right bg-[#fbfbfd]/95 backdrop-blur-xl p-6 shadow-xl"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="space-y-1">
              {navItems.map((item) => (
                <Link
                  key={item.href}
                  href={item.href}
                  onClick={() => setMobileMenuOpen(false)}
                  className={`block rounded-xl px-4 py-3 text-[17px] font-normal transition-colors ${
                    pathname === item.href
                      ? "bg-[#f5f5f7] text-[#1d1d1f]"
                      : "text-[#6e6e73] hover:bg-[#f5f5f7] hover:text-[#1d1d1f]"
                  }`}
                >
                  {item.label}
                </Link>
              ))}
            </div>

            <div className="mt-6 border-t border-[#d2d2d7]/40 pt-6">
              {user ? (
                <div className="space-y-3">
                  <Link
                    href={getDashboardLink()}
                    onClick={() => setMobileMenuOpen(false)}
                    className="flex h-[48px] w-full items-center justify-center rounded-full bg-[#0071e3] text-[17px] font-normal text-white transition-all hover:bg-[#0077ed]"
                  >
                    Panel
                  </Link>
                  <button
                    onClick={() => {
                      handleSignOut();
                      setMobileMenuOpen(false);
                    }}
                    className="flex h-[48px] w-full items-center justify-center rounded-full border border-[#d2d2d7] text-[17px] font-normal text-[#1d1d1f] transition-all hover:border-[#86868b] hover:bg-[#f5f5f7]"
                  >
                    Çıkış Yap
                  </button>
                </div>
              ) : (
                <Link
                  href="/auth/login"
                  onClick={() => setMobileMenuOpen(false)}
                  className="flex h-[48px] w-full items-center justify-center rounded-full bg-[#0071e3] text-[17px] font-normal text-white transition-all hover:bg-[#0077ed]"
                >
                  Giriş Yap
                </Link>
              )}
            </div>
          </div>
        </div>
      )}
    </>
  );
}
